html {
	overflow-y: scroll;
}
body {
	color: $font-color;
	font-family: $font-style;
	font-size: 1.1em;
	line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	display: flex;
	width: 100%;
}

h1, h2, h3 {
	font-weight: 600;
	color: $heading-font-color;
	font-family: $heading-font;
	line-height: 1.5;
}

h1 {
	font-size: 1.5em;
	a,a:visited {
		text-decoration: none;
		color: $link-color;
	}
	a:hover {
		text-decoration: none;
		color: $link-hover-color;
	}
	a:active {
		color: $link-active-color;
	}
}
h2 {
	font-size: 1.7em;
	a,a:visited {
		text-decoration: none;
		padding: 0;
		margin: 0;
	}
	a:hover {
		text-decoration: none;
		color: $link-hover-color;
	}
	a:active {
		color: $link-active-color;
	}
}

.btn-primary,.btn-primary:visited {
	text-decoration: none;
	font-weight: bold;
	color: white;
	background-color: $link-hover-color;
	border-color: $link-hover-color;
}

.collapsed, .collapsed:visited {
	text-decoration: none;
	font-weight: bold;
	color: white;
	background-color: $link-color;
	border-color: $link-color;
}

.btn-primary:hover {
	text-decoration: none;
	font-weight: bold;
	color: white;
	background-color: $link-hover-color;
	border-color: $link-hover-color;
}

.btn-primary:active {
	text-decoration: none;
	background-color: $link-active-color;
	border-color: $link-active-color;
	color: white;
	outline: none;
	box-shadow: none;
}

/* Remove border around button after click */
.btn:active:focus:hover {
	outline: none;
	box-shadow: none;
}

a,a:visited {
	color: $link-color;
	text-decoration: none;
	font-weight: bold;
}
a:hover {
	text-decoration: none;
	color: $link-hover-color;
}

a:active {
	text-decoration: none;
	color: $link-active-color;
}

main {
	width: 100%;
	max-width: $content-width;
	margin: 0 auto;
	text-align: justify;
	text-justify: auto;
}

article {
	max-width: $content-width;
	width: 100%;
	margin: 0 auto;
}

time {
	color: #898989;
}
.container {
	max-width: $content-width;
	margin: 0 auto;
}

#me {
	display: block;
	margin: 0 auto;
	width: 20%;
}

.bibliography li {
	list-style-type: none;
}
.bibliography {
	padding: 0;
}

.title {
	font-size: 1.1em;
}

.authors {
	font-size: 0.8em;
}

.card {
	font-size: 0.9em;
	max-width: $content-width;
}

.font-italic {
	font-style: italic;
}

aside {
	background: #fff;
	z-index: 2;
	border-bottom: 1px solid lighten($light, 30%);
	box-shadow: 0px 1px 1px RGBA(4, 25, 54, 0.1);
	padding: 0;
	color: $link-color;
}

aside i {
	font-size: 2em;
	color: $link-color;
}

aside a:hover, aside i:hover {
	color: $link-hover-color;
}

aside a:active, aside i:active {
	color: $link-active-color;
}

nav {
	width: 100%;
	max-width: $content-width;
	margin: 0 auto;
}
.navbar-light {
	padding: 0;
}
.fixed-bottom {
	max-width: $content-width;
	margin: 0 auto;  
}

footer {
	width: 100%;
	height: 100%;
	max-width: $content-width;
	font-size: 0.9em;
}